.resume {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .section-heading {
    margin-top: 20px;
    margin-bottom: 10px;
    border-bottom: 2px solid #171616; /* Add underline with 2px solid line */
  }
  
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  ul li {
    margin-bottom: 10px;
  }
  
  p {
    margin-bottom: 20px;
  }
  
  /* Additional styles for better readability */
  .resume h1 {
    font-size: 1.5rem;
  }
  
  .resume p {
    font-size: 1rem;
  }
  
  .resume ul {
    font-size: 1rem;
    padding-left: 20px;
  }
  
  .resume ul {
    list-style-type: disc; /* Use disc for bullet points */
  }
  
  .resume ul li {
    font-size: 1rem;
  }
  
  
  .resume p, .resume ul {
    line-height: 1.5;
  }
  