.light {
  /* Light mode styles */
  background: linear-gradient(to bottom right, rgba(139, 92, 246, 0.8), rgba(59, 130, 246, 0.8), rgba(75, 85, 99, 0.8)); /* light gradient background with opacity */
  color: #171616; /* light text color */
}


.dark {
  /* Dark mode styles
  bg-gradient-to-br from-purple-900 via-blue-900 to-gray-800 min-h-screen w-full flex items-center justify-center">
  */
  background: linear-gradient(to bottom right, #6945bd, #27418a, #1E293B); /* darker gradient background */
  color: #ffffff; /* dark text color */
}
.animate-spin-slow {
  animation: spin-slow 4s linear infinite; /* Adjust duration as needed */
}
@keyframes spin-slow {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}